.header{
	position: fixed;
    top: 0px;
    background-color: rgba(255,255,255, 1);
    width: 100%;
    z-index: 1;    
    transition: 0.5s;    
}
.header.collapse {
    transition: 0.5s;
}
.headerContent {
    position: relative;
    transition: 0.5s;
}
.title {
	text-align: center;
    transition: 0.5s;
	font-family: "Great Vibes", cursive, serif; 
	color: #636769;
	font-size: 48pt;
	font-weight: normal;
}
.amp {
    transition: 0.5s;
	font-family: 'Playfair Display', serif;
	opacity: .8;
	font-size: 108pt;
}
.lname {
	font-size: 24pt;
	display: none;
    transition: 0.5s;
}
.firstName{
    transition: 0.5s;
	position: relative;
	display: inline-block;
  bottom: 33px;
  padding-right: 10px;
}
.secondName {
    transition: 0.5s;
	position: relative;
	display: inline-block;
  bottom: 33px;
  padding-left: 10px;
}
.header.collapse {
    height: 132px;
    background-color: rgba(255,255,255,1);
    transition: 0.5s;
    text-align: center;
}
.header.collapse .amp {
  font-size: 48pt;
}

.headImage, .faqSection, #root>.section {
  margin-top: 132px;
}

